$black: #171717;
$pure-black: #000;
$white: #fff;

$transparent-heavy: rgba(0, 0, 0, 0.8);
$transparent-medium: rgba(0, 0, 0, 0.6);
$transparent-light: rgba(0, 0, 0, 0.4);

$theme-color-base: #e8e8e8;
$theme-color-accent-light: #9ba79d;


/* DARK MODE */
$dm-black: rgba(255, 255, 255, 0.7);
$dm-white: #545655;

$dm-transparent-heavy: rgba(255, 255, 255, 0.8);
$dm-transparent-medium: rgba(255, 255, 255, 0.6);
$dm-transparent-light: rgba(255, 255, 255, 0.4);

$dm-theme-color-base: #2c2d2d;
$dm-theme-color-accent-light: #9ba79d;

$space-xs: .2em;
$space-s: .4em;
$space-m: $space-s * 2;
$space-l: $space-s * 4;
$space-xl: $space-s * 5;


.spacer-top-s {
  margin-top: $space-s;
}
.spacer-top-m {
  margin-top: $space-m;
}


// NEW VARIABLES

$multiplier-1: 1.05;
$multiplier-2: 1.1;
$multiplier-3: 1.15;

$font-header: 'Kanit', sans-serif;
$font-content: 'Kanit', sans-serif;

$font-weight-light: 200;
$font-weight-regular: 500;
$font-weight-heavy: 700;
$font-height: 1.55em;

$font-size-s: 0.9rem;
$font-size-m: 1.1rem; // H4, p, a, etc
$font-size-l: 1.7rem; // H3
$font-size-xl: 2.0rem; // H2
$font-size-xxl: 2.6rem; // H1

$font-color: rgba(0, 0, 0, 0.8);
$font-color-light: rgba(0, 0, 0, 0.6);

$breakpoint-small: 600px; // 540px
$breakpoint-med: 1200px; // 720px
$breakpoint-large: 1600px; // 960px



$color-p1: #e6d717; // body
$color-p2: #009fbf; // theme blue
$color-p3: #00697e; // theme blue dark
$color-p4: #00697e; // theme blue darker
$color-s1: #ffdbad; // orange
$color-s2: #3a3a3a; // dark orange
.p1 { background-color: $color-p1; h2, h3, p, a, li { color: $font-color; } }
.p2 { background-color: $color-p2; h2, h3, p, a, li { color: $font-color; } }
.p3 { background-color: $color-p3; h2, h3, p, a, li { color: $font-color; } }
.p4 { background-color: $color-p4; h2, h3, p, a, li { color: $font-color; } }
.s1 { background-color: $color-s1; h2, h3, p, a, li { color: $font-color; } }
.s2 { background-color: $color-s2; h2, h3, p, a, li { color: $font-color; } }
