@import "variables";
@import "franklabs-layout";

html, body {
  background-color: $color-s1;
  @media screen and (prefers-color-scheme: dark) {
      background-color: $color-s2;
    }
}

// h1, h2, h3, h4 {
//   color: $black;
//   @media screen and (prefers-color-scheme: dark) {
//     color: $dm-transparent-heavy;
//   }
// }

:root {
  color-scheme: light;
}
@media screen and (prefers-color-scheme: dark) {
  :root {
    color-scheme: dark;
  }
}

header {
  position: relative;
  margin: 0 auto 25px;
  // xmax-width: 80%;
  // width: 90%;
  .logo {
    display: inline-block;
    vertical-align: bottom;
    margin-top: 30px;
    &:hover {
      background-color: transparent;

    }
    &:focus {
      background-color: #d0b28d;
      @media screen and (prefers-color-scheme: dark) {
        background-color: #6b655c;
      }
    }
  }
  .logo, .franklabs a {

    text-decoration: none;
  }
  .franklabs {
    font-size: 2.1em;
    &:hover {
      text-decoration: underline;
    }
  }

  @media only screen and (min-width: $breakpoint-small) {
    width: 80%;
    max-width: 50rem;
  }

  @media only screen and (min-width: $breakpoint-large) {
    width: 75%;
    max-width: 60rem;
  }


  img {
    width: 245px;
    display: inline-block;
    vertical-align: bottom;
    filter: invert(0);

    @media screen and (prefers-color-scheme: dark) {
      filter: invert(1);
    }

  }

  .menu {
    margin: 35px auto 0px;
    ul {
      li {
        display: inline-block;
        margin-right: .6em;
      }
    }

  }
}

.company-data {
  img.nbpo-logo {
    width: 100%;
  }
  .linkedin-logo {
    width: 30px;
    margin: 14px 8px -8px 0px;
    @media screen and (prefers-color-scheme: dark) {
      filter: invert(1);
    }
  }
}


.container {
  position: relative;



  h2 {
    i {
      // the labs in franklabs
      font-weight: 600;
      letter-spacing: -3.8px;
    }
  }

  .metadata {
    position: relative;
    ul {
      margin-bottom: 2em;
      li {
        font-style: italic;
        color: $transparent-medium;
        @media screen and (prefers-color-scheme: dark) {
          color: $dm-transparent-heavy;
        }
        margin-left: 49px;
      }
    }
    .avatar {
      float: right;
      position: absolute;
      top: -2px;
      left: 0px;
      width: 36px;
      @media screen and (min-width: 1200px) {
        float: none;
        position: relative;
        top: 0px;
        width: 104px;
        margin-top: 10px;
      }
    }
    @media screen and (min-width: 1200px) {
      position: absolute;
      right: -267px;
      width: 250px;
      ul {
        li {
          margin-left: 0;
        }
      }
    }
  }

  .spacer-ava-gr1200 {
    @media screen and (min-width: 1200px) {
      height: 60px;
      width: 100%;
    }
  }

  figure {
    margin: 0em 0px 1.2em;
    &.right-small {
      width: 100%;
      @media screen and (min-width: $breakpoint-small) {
        width: 150px;
        padding-bottom: 150px;
      }
      @media screen and (min-width: $breakpoint-med) {
        width: 250px;
        padding-bottom: 250px;
      }
      position: relative;
      float: right;
      margin: 0px 0px 20px 40px;
      padding-bottom: 100%;
      height: 0;
    }
    img {
      width: 100%;
      @media screen and (prefers-color-scheme: dark) {
        filter: brightness(0.7) contrast(1.2);
      }
      // box-shadow: -69px 0px 54px -10px rgba(20, 36, 47, 0.6);
    }
    figcaption {
      width: 100%;
      margin: 1em 0;
      text-align: center;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-black;
      }
    }

  }

  .intro {
    border-bottom: 1px solid $theme-color-accent-light;
    @media screen and (prefers-color-scheme: dark) {
      border-bottom: 1px solid $dm-theme-color-accent-light;
    }
    padding-bottom: 1em;
  }

  .intermezzo {
    border: 1px solid black;
    @media screen and (prefers-color-scheme: dark) {
      border: 1px solid $dm-black;
    }
    padding: 1em 5% 0.4em;
    margin: 1.3em 0 1.1em;
    @media screen and (min-width: 600px) {
      border: 1px solid black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
    }
    @media screen and (min-width: 1200px) {
      border: 1px solid black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
      margin: 1.4em 0 1.2em;
    }
    @media screen and (min-width: 1600px) {
      position: absolute;
      right: -379px;
      width: 250px;
      margin-top: 0px;
      padding: 1.4em 5% 1.0em;
    }
    h4 {
      margin-top: 0px;
    }
  }

  .button {
    background-color: $transparent-light;
    border: 1px solid rgba(0, 0, 0, 0);
    color: $white;
    padding: 1px 13px 4px;
    text-decoration: none;
    -webkit-font-smoothing: antialiased;
    border-radius: 22px;
    &:hover {
      border: 1px solid $black;
      background-color: transparent;
      color: $black;
      cursor: pointer;
    }
    &.inverse {
      background-color: $white;
      @media screen and (prefers-color-scheme: dark) {
        background-color: $dm-white;
      }
      border: 1px solid $black;
      @media screen and (prefers-color-scheme: dark) {
        border: 1px solid $dm-black;
      }
      color: $black;
      @media screen and (prefers-color-scheme: dark) {
        color: $dm-black;
      }
      &:hover {
        background-color: $transparent-light;
        @media screen and (prefers-color-scheme: dark) {
          background-color: $dm-transparent-light;
        }
        border: 1px solid rgba(0, 0, 0, 0);
        color: $white;
        @media screen and (prefers-color-scheme: dark) {
          color: $dm-white;
        }
      }
    }
  }

  .button-large {
    display: inline-block;
    padding: 10px 12px;
    margin-right: 6px;
    //border-radius: 8px;
    text-decoration: none;
    color: $white;
    font-weight: 500;
    background: $color-p3;
    //box-shadow: -6px 0px 12px -4px #969696, 2px 0px 13px #fff;
    &:hover {
      background: $black;
    }
    @media screen and (prefers-color-scheme: dark) {
      filter: invert(1);
    }
    xbackground: linear-gradient(204deg, #dadfdf, #a5a8a8);

  }

  .image-homepage,
  .image-over {
    object-fit: cover;
    // border-radius: 8px;
  }

  .image-homepage {
    height: 260px;
    @media only screen and (min-width: $breakpoint-small) {
      height: 360px;
    }
  }

  .pricing {
    // border-radius: 7px;
    // padding: 0px 8px;
    // margin: 6px 0px 0px -8px;
    // display: inline-block;
    // color: $white;
    // background-color: $color-p3;

    padding: 0px 8px;
    margin: 0px 8px 0px 0px;
    display: inline-block;
    border: 1px solid $black;
    @media screen and (prefers-color-scheme: dark) {
      border: 1px solid $white;
    }
  }

  .gradient-text {
    background: -webkit-linear-gradient(left,#2c424d, #557880);
    background: linear-gradient(to right,#2c424d, #557880);
        background-clip: border-box;
    color: rgba(233,70,150,0);
    -webkit-background-clip: text;
    @media screen and (prefers-color-scheme: dark) {
      background: -webkit-linear-gradient(left,#627e7b, #77938b);
      background: linear-gradient(to right,#627e7b, #77938b);
          background-clip: border-box;
      color: rgba(233,70,150,0);
      -webkit-background-clip: text;
    }
  }

  h2 {
    position: relative;
    @media only screen and (min-width: $breakpoint-small) {
      position: relative;
    }
  }



  .header-icon:before {
    content: "";
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    left: 86%;
    top: -7px;
    @media only screen and (min-width: $breakpoint-small) {
      left: -70px;
      top: 0px;
    }
    @media screen and (prefers-color-scheme: dark) {
      filter: invert(1);
    }
  }
  .header-icon-checklist:before {
    background: url("/img/icons-po/checklist.png") no-repeat;
    background-size: contain;
  }
  .header-icon-inbox:before {
    background: url("/img/icons-po/inbox.png") no-repeat;
    background-size: contain;
  }
  .header-icon-list:before {
    background: url("/img/icons-po/list.png") no-repeat;
    background-size: contain;
  }
  .header-icon-checkbox:before {
    background: url("/img/icons-po/checkbox.png") no-repeat;
    background-size: contain;
  }
  .header-icon-poppetje-wijst:before {
    background: url("/img/icons-po/poppetje-wijst.png") no-repeat;
    background-size: contain;
  }
  .header-icon-poppetje-steekt-hand-op:before {
    background: url("/img/icons-po/poppetje-steekt-hand-op.png") no-repeat;
    background-size: contain;
  }
  .header-icon-poppetje-springt:before {
    background: url("/img/icons-po/poppetje-springt.png") no-repeat;
    background-size: contain;
  }
  .header-icon-gezichtje-iets-schuin:before {
    background: url("/img/icons-po/gezichtje-iets-schuin.png") no-repeat;
    background-size: contain;
  }
  .header-icon-gezichtje-met-kuif:before {
    background: url("/img/icons-po/gezichtje-met-kuif.png") no-repeat;
    background-size: contain;
  }
  .header-icon-list:before {
    background: url("/img/icons-po/list.png") no-repeat;
    background-size: contain;
  }
  .header-icon-email:before {
    background: url("/img/icons-po/email.png") no-repeat;
    background-size: contain;
  }
  .header-icon-phone:before {
    background: url("/img/icons-po/phone.png") no-repeat;
    background-size: contain;
  }



}
