
@import "variables.scss";


// universal

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  font-family: $font-content;
  color: $font-color;
  @media screen and (prefers-color-scheme: dark) {
    color: $dm-black;
  }
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-moz-selection,
::selection {
  // color: $color-ti;
  // background: $color-t;
}


// typography


h1, h2, h3, h4, h1 span, h2 span, h3 span, h4 span {
  font-family: $font-header;
  font-color: $font-color;
  font-weight: $font-weight-regular;
  letter-spacing: -0.6px;
  line-height: $font-height * 0.9;
  margin: 0 0 0.33em;
}

h1, h2, h3, h4 {
  //@extend .gradient-text;
}

h1 {
  font-size: $font-size-xxl;
  &:first-of-type {
    margin: 0 0 0.6em;
  }
  @media screen and (min-width: $breakpoint-small) {
    font-size: $font-size-xxl * $multiplier-1;
  }
  @media screen and (min-width: $breakpoint-med) {
    font-size: $font-size-xxl * $multiplier-2;
  }
  @media screen and (min-width: $breakpoint-large) {
    font-size: $font-size-xxl * $multiplier-3;
  }


}

h2 {
  font-size: $font-size-xl;
  @media screen and (min-width: $breakpoint-small) {
    font-size: $font-size-xl * $multiplier-1;
  }
  @media screen and (min-width: $breakpoint-med) {
    font-size: $font-size-xl * $multiplier-2;
  }
  @media screen and (min-width: $breakpoint-large) {
    font-size: $font-size-xl * $multiplier-3;
  }
}

h3 {
  font-size: $font-size-l;
  @media screen and (min-width: $breakpoint-small) {
    font-size: $font-size-l * $multiplier-1;
  }
  @media screen and (min-width: $breakpoint-med) {
    font-size: $font-size-l * $multiplier-2;
  }
  @media screen and (min-width: $breakpoint-large) {
    font-size: $font-size-l * $multiplier-3;
  }
}

h4 {
  font-size: $font-size-m;
  line-height: $font-height;
  @media screen and (min-width: $breakpoint-small) {
    font-size: 1.3em;
  }
  @media screen and (min-width: $breakpoint-med) {
    font-size: 1.4em;
  }
  @media screen and (min-width: $breakpoint-large) {
    font-size: 1.47em;
  }
}

p, a, li, input, textarea {
  font-size: $font-size-m;
  line-height: $font-height;
  font-weight: $font-weight-light;
  @media screen and (min-width: $breakpoint-small) {
    font-size: $font-size-m * $multiplier-1;
  }
  @media screen and (min-width: $breakpoint-med) {
    font-size: $font-size-m * $multiplier-2;
  }
  @media screen and (min-width: $breakpoint-large) {
    font-size: $font-size-m * $multiplier-3;
  }
}

i, figcaption {
  font-style: italic;
}

p, ul, ol {
  margin-bottom: 1.2em;
}

strong {
  //font-weight: bold;
  color: $pure-black;
  letter-spacing: -0.02em;
  font-weight: 500;
  a {
    font-weight: 500;
  }
  @media screen and (prefers-color-scheme: dark) {
    color: $white;
  }
}


ul, ol {
  list-style-type: none;
  &.list, &.ordered-list {
    margin-left: 1em;
  }
  &.list {
    li {
      list-style-type: disc;
      padding-left: 5px;
    }
  }
  &.ordered-list {
    li {
      list-style-type: decimal;
    }
  }
}
a {
  &:hover {
    background-color: $black;
    color: $color-s1;
    @media screen and (prefers-color-scheme: dark) {
      background-color: $white;
      color: $black;
    }
  }
  &.img-link {
    &:hover {
      background-color: transparent;
    }
  }
  &:focus {
    background-color: #d0b28d;
    color: $black;
    @media screen and (prefers-color-scheme: dark) {
      background-color: #6b655c;
      color: $white;
    }
  }
}



input[type="email"], input[type="text"],
select,
textarea {
  background-color: $white;
  font-family: $font-content;
  padding: 5px;
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
  width: 100%;
  font-weight: inherit;
}


input[type="email"], input[type="text"] {
  background-color: $white;
  font-family: $font-content;
}

input[type="submit"], button {
  font-weight: inherit;
}

select {
  background-color: $white;
  font-family: $font-content;
}

@media screen and (prefers-color-scheme: dark) {
  option {
    color: $black;
  }
}

textarea {
  height: 200px;
  min-height: 200px;
  max-width: 100%;
  min-width: 100%;
}

.intro {
  font-size: $font-size-m * $multiplier-1;
  @media screen and (min-width: $breakpoint-small) {
    font-size: $font-size-m * $multiplier-1 * $multiplier-1;
  }
  @media screen and (min-width: $breakpoint-med) {
    font-size: $font-size-m * $multiplier-1 * $multiplier-2;
  }
  @media screen and (min-width: $breakpoint-large) {
    font-size: $font-size-m * $multiplier-1 * $multiplier-3;
  }
}

.metadata {
  ul {
    li {
      @extend .small;
    }
  }
}

.intermezzo {
  p, a, li {
    @extend .small;
  }
}

figcaption {
  font-size: $font-size-s;
  color: $font-color-light;
  @media screen and (prefers-color-scheme: dark) {
    //color
  }
  margin-bottom: 0.8em;
  @media screen and (min-width: $breakpoint-small) {
    font-size: $font-size-s * $multiplier-1;
  }
  @media screen and (min-width: $breakpoint-med) {
    font-size: $font-size-s * $multiplier-2;
  }
  @media screen and (min-width: $breakpoint-large) {
    font-size: $font-size-s * $multiplier-3;
  }
}

hr {
  height: 2px;
  border-top: 5px solid $black;
  margin: 90px 0 40px;
  @media screen and (prefers-color-scheme: dark) {
    border-top: 5px solid $white;
  }

  &.small {
    height: 0px;
    border-top: 0px;
    border-bottom: 1px solid $black;
    margin: 70px 0 40px;
    @media screen and (prefers-color-scheme: dark) {
      border-bottom: 1px solid $white;
    }
  }
}




.nohyperlinks {
  pointer-events: none;
  > a { text-decoration:none; color:inherit; }
}

// utility

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.justify {
  text-align: justify;
}

.small {
  font-size: $font-size-s;
  line-height: $font-height * 0.9;
}

.hidden-sm {
  display: none;
}

.spacer-top-m {
  padding-top: 21px;
}
.spacer-top-l {
  padding-top: 35px;
}
.spacer-bottom-l {
  padding-bottom: 35px;
}

.hidden {
  display: none;
}


// grid

$width: 94%;
$gutter: 100 - $width;

.container, .footer {
  margin-left: auto;
  margin-right: auto;

  @media only screen and (min-width: $breakpoint-small) {
    width: 80%;
    max-width: 50rem;
  }

  @media only screen and (min-width: $breakpoint-large) {
    width: 75%;
    max-width: 60rem;
  }
}

.company-data {
  margin-top: 4em;
  padding-bottom: 20em;
  .divider {
    margin: 0px 20px;
    background-color: $black;
    display: block;
    width: 0px;
    height: 0em;
    @media only screen and (min-width: $breakpoint-small) {
      display: inline-block;
      width: 1px;
      height: .8em;
    }
    @media screen and (prefers-color-scheme: dark) {
      background-color: $white;
    }
  }
  p {
    font-size: $font-size-m;
    color: $black;
    @media screen and (prefers-color-scheme: dark) {
      color: $dm-black;
      > a {
        color: $dm-black;
      }
    }
  }
}

.row {
  position: relative;
  width: 100%;
}

.row [class^="col"] {
  float: left;
  margin: ($gutter/3) ($gutter/2);
  min-height: 0.125rem;
}

.row::after {
  content: "";
  display: table;
  clear: both;
}

.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
  width: $width;
}

.col-1-sm { width:($width / 12) - ($gutter * 11 / 12); }
.col-2-sm { width: ($width / 6) - ($gutter * 10 / 12); }
.col-3-sm { width: ($width / 4) - ($gutter * 9 / 12); }
.col-4-sm { width: ($width / 3) - ($gutter * 8 / 12); }
.col-5-sm { width: ($width / (12 / 5)) - ($gutter * 7 / 12); }
.col-6-sm { width: ($width / 2) - ($gutter * 6 / 12); }
.col-7-sm { width: ($width / (12 / 7)) - ($gutter * 5 / 12); }
.col-8-sm { width: ($width / (12 / 8)) - ($gutter * 4 / 12); }
.col-9-sm { width: ($width / (12 / 9)) - ($gutter * 3 / 12); }
.col-10-sm { width: ($width / (12 / 10)) - ($gutter * 2 / 12); }
.col-11-sm { width: ($width / (12 / 11)) - ($gutter * 1 / 12); }
.col-12-sm { width: $width; }

@media only screen and (min-width: $breakpoint-med) {
  .col-1 { width:($width / 12) - ($gutter * 11 / 12); }
  .col-2 { width: ($width / 6) - ($gutter * 10 / 12); }
  .col-3 { width: ($width / 4) - ($gutter * 9 / 12); }
  .col-4 { width: ($width / 3) - ($gutter * 8 / 12); }
  .col-5 { width: ($width / (12 / 5)) - ($gutter * 7 / 12); }
  .col-6 { width: ($width / 2) - ($gutter * 6 / 12); }
  .col-7 { width: ($width / (12 / 7)) - ($gutter * 5 / 12); }
  .col-8 { width: ($width / (12 / 8)) - ($gutter * 4 / 12); }
  .col-9 { width: ($width / (12 / 9)) - ($gutter * 3 / 12); }
  .col-10 { width: ($width / (12 / 10)) - ($gutter * 2 / 12); }
  .col-11 { width: ($width / (12 / 11)) - ($gutter * 1 / 12); }
  .col-12 { width: $width; }



  .hidden-sm {
    display: block;
  }
}
